
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.btn-list{
    overflow: hidden;
    position: absolute;
    left: 10%;
    width: 250px;
    line-height: 30px;
}
.btn-list button{
    margin-left: 5px;
}
.probxx-box{
    position: absolute;
    left: 15%;
    top: 170px;
}
.result-box{
    float: right;
    width: 60%;
    border: solid 1px black;
    height: 800px;
    overflow-y: auto;
    position: absolute;
    right: 20px;
    top: 20px;
}
.result-item{
    text-align: left;
}
.result-item span{
    display: inline-block;
    width: 100px;
    text-align: center;
    margin-bottom: 10px;
}
.four{
    color: #0067ed;
}
.five{
    color: red;
}
